.Chart{
    width: 55%;
    height: 70%;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
    scroll-behavior: auto;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background: white;
    margin-top: 50px;
    border-radius: 3px;
    z-index: 5;
}

.ChartDock{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
    background: #63A9FF;
    height: 60px;
    width: 100%;
    z-index: 4;
    padding-left: 15px;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
}

.ChartDock h3{
    color: white;
}

.Chart:hover,
.Chart:active {
    -webkit-transform: scale(1.001);
        -ms-transform: scale(1.001);
            transform: scale(1.001);
}

@media(max-width: 500px){
    .Chart{
        width: 90%;
        height: 80%;
    }
}



