body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-image: -webkit-gradient(linear, right top, left top, from(#89f7fe), color-stop(#63e6ff), color-stop(#48d3ff), color-stop(#4abeff), to(#66a6ff));
  background-image: -webkit-linear-gradient(right, #89f7fe, #63e6ff, #48d3ff, #4abeff, #66a6ff);
  background-image: -o-linear-gradient(right, #89f7fe, #63e6ff, #48d3ff, #4abeff, #66a6ff);
  background-image: linear-gradient(to left, #89f7fe, #63e6ff, #48d3ff, #4abeff, #66a6ff);
}



