.Header{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
}
.HeaderTabs{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: center;
        justify-content: center;
}
.Header h1{
    padding: 15px 0 15px 0;
    color:white; 
}

.HeaderTabs ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    text-align: center;

  }

  .HeaderTabs li{
      display: inline;
      margin: 20px;
  }
.HeaderTabs a{
    background: transparent;
    border-color:#fff;
    border-style: solid;
    color: white;
    padding: 10px;
    width: 80px;
    border-width: thin;
    text-decoration:none;
    display: table-cell;
}

a:hover{
    background-color: #63A9FF;
    color: white;
    border-color:#63A9FF;
    border-style: solid;
    -webkit-transform: scale(1.01);
        -ms-transform: scale(1.01);
            transform: scale(1.01);
  }


